@use '../../../../styles/mixins/respond' as *;
@use '../../../../styles/mixins/position' as *;

.SentencesConstructor {
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--color-explorer-blue);
	width: 100%;
	margin: auto;

	> .task-name {
		color: rgba(255, 255, 255, 0.4);
		font-size: var(--fs-lg);
		padding: 20px;
	}

	> .answer {
		font-size: var(--fs-h3);
		color: var(--color-white);
		min-height: 90px;
		max-width: 90%;
		width: 100%;
		margin-bottom: var(--spacing-quarter);
		position: relative;
		padding-left: 50px;
		padding-right: 50px;
		transition: height .8s linear;
	}
}

.answered-sentence {
	display: flex;
	gap: var(--spacing-quarter);
	flex-wrap: wrap;

	> .-wrong {
		text-decoration: line-through var(--color-red);
		color: var(--color-grey);
	}
}

.current-sentence {
	color: var(--color-white);
	font-size: var(--fs-h4);
	padding: 10px 20px 20px;
}

.sentence-tiles {
	border-radius: var(--standard-border-radius);
	box-shadow: 0 2px 2px 0 rgb(0 0 0 / 13%);
	color: #333;
	background-color: var(--color-white);
	font-weight: var(--fw-semi-bold);
	font-size: var(--fs-h4);
	user-select: none;
    max-width: 90%;
	width: 100%;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
	word-break: break-word;

	@include respond(mobile-large desktop) {
		min-width: 600px;
   		width: 650px;
	}

	> li {
		width: calc(100% / 3);
		cursor: pointer;
		border: 1px solid #E6E9EE;
		height: 90px;
		display: flex;
		justify-content: center;
		align-items: center;
		overflow: auto;

		&:focus {
			background-color: var(--color-light-grey);
		}
	}
}

.progress-bar-holder {
	height: 80px;
	background-color: #4b66a1;
	flex-grow: 1;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.progress-bar {
	text-align: center;
	position: relative;
}

.progress-dot {
	display: inline-block;
	width: var(--spacing-half);
	height: var(--spacing-half);
	margin-right: 24px;
	border-radius: 50%;
	background: #435b91;
	z-index: 2;
}

.progress-dot:last-child {
	margin-right: 0;
}

.progress-dot.-current {
	position: relative;
}

.progress-dot.-current::after {
	content: '';
    display: block;
    height: 6px;
    width: 6px;
    border-radius: 50%;
    background-color: var(--color-white);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.SentencesConstructor .progress-bar::after {
	content: '';
    display: block;
    height: 6px;
    width: calc(100% - 12px);
    background-color: #435b91;
    position: absolute;
    left: 6px;
    top: calc(50% - 4px);
    transform: translateY(-50%);
}


.voice-over-icon {
    width: 40px;
    height: 40px;
    background: url(../../../../static_assets/images/voice-over.svg) center no-repeat;
	opacity: .5;
	pointer-events: none;

	@include absolute(0, null, null, 0);

	&.-active {
		opacity: 1;
		pointer-events: auto;
		cursor: pointer;
	}
}

.delete-word {
	cursor: pointer;
	background: url(data:image/svg+xml;base64,);
	height: 20px;
    width: 26px;

	@include absolute(8px, 0);
}

.-hidden {
	display: none !important;
}