//Create position for setting parameters

@mixin position($position, $top:0, $right:0, $bottom:0, $left:0) {
	bottom: $bottom;
	left: $left;
	position: $position;
	right: $right;
	top: $top;
}

@mixin absolute($top:null, $right:null, $bottom:null, $left:null) {
	position: absolute;

	@if $top {
		top: $top;
	}

	@if $right {
		right: $right;
	}

	@if $bottom {
		bottom: $bottom;
	}

	@if $left {
		left: $left;
	}
}

@mixin absolute-same($position) {
	@include absolute($position, $position, $position, $position);
}

@mixin absolute-center() {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}